    aeAensUpdate: async function (secretKey, name, address, pointers, isPointers) {
      try {
        let nameInfo = await aeSdk.api.getNameEntryByName(name);
        if (nameInfo == null) {
          this.responseError("aeAensUpdate", "name is not exist");
          return;
        }
        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount(secretKey);
        // const pointers = {
        //   account_pubkey: address,
        // };

        aeSdk.addAccount(senderAccount, { select: true });
        let spendTxResult;
        if (isPointers) {
          spendTxResult = await aeSdk.aensUpdate(name, pointers, { extendPointers: true });
        } else {
          spendTxResult = await aeSdk.aensUpdate(name, {}, { extendPointers: true });
        }

        this.responseSuccess("aeAensUpdate", { hash: spendTxResult.hash });
      } catch (e) {
        this.responseError("aeAensUpdate", e.message);
      }
    },

    aeAensClaim: async function (secretKey, name) {
      let publicKey;
      try {
        if (!(await this.getNameInfo(name))) {
          this.responseError("aeAensClaim", "Name is registered");
          return;
        }
        publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount(secretKey);
        aeSdk.addAccount(senderAccount, { select: true });
        const preClaimTx = await aeSdk.aensPreclaim(name);
        const claimTx = await aeSdk.aensClaim(name, preClaimTx.salt, {
          verify: true,
          waitMined: false,
        });

        this.responseSuccess("aeAensClaim", { hash: claimTx.hash });
      } catch (e) {
        this.responseError("aeAensClaim", e.message);
      }
    },

    getNameInfo: async function (name) {
      try {
        await aeSdk.api.getNameEntryByName(name);
        return false;
      } catch (e) {
        return true;
      }
    },

