        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount(secretKey);
        aeSdk.addAccount(senderAccount, { select: true });
        let spendTxResult = await aeSdk.aensTransfer(name, recipient);
        this.responseSuccess("aeAensTransfer", { hash: spendTxResult.hash });
      } catch (e) {
        this.responseError("aeAensTransfer", e.message);
      }
    },


