    aeAex9TokenCreateAllowance: async function (
      secretKey,
      ctAddress,
      forAddress,
      amount
    ) {
      try {
        forAddress = forAddress.toString().replace("ct_", "ak_");
        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount(secretKey);
        aeSdk.addAccount(senderAccount, { select: true });
        let aex9Contract = await aeSdk.initializeContract({
          aci: Aex9TokenContract,
          address: ctAddress,
        });

        let tx = await aex9Contract.create_allowance(
          forAddress,
          toAettos(amount)
        );

        this.responseSuccess("aeAex9TokenCreateAllowance", {
          hash: tx.hash,
        });
      } catch (e) {
        if (e.message.toString().includes("ALLOWANCE_ALREADY_EXISTENT")) {
          console.log("ALLOWANCE_ALREADY_EXISTENT");
          try {
            forAddress = forAddress.toString().replace("ct_", "ak_");
            const publicKey = getAddressFromPriv(secretKey);
            const senderAccount = new MemoryAccount(secretKey);
            aeSdk.addAccount(senderAccount, { select: true });
            let aex9Contract = await aeSdk.initializeContract({
              aci: Aex9TokenContract,
              address: ctAddress,
            });

            let tx = await aex9Contract.change_allowance(
              forAddress,
              toAettos(amount)
            );

            this.responseSuccess("aeAex9TokenCreateAllowance", {
              hash: tx.hash,
            });
          } catch (e) {
            this.responseError("aeAex9TokenCreateAllowance", e.message);
          }
        } else {
          this.responseError("aeAex9TokenCreateAllowance", e.message);
        }
      }
    },
    aeAex9TokenAllowance: async function (ctAddress, address, forAddress) {
